.signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(1, 1, 1, 0.7);
  z-index: 1;
}

.signup form {
  background: white;
  padding: 15px;
  margin: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 70%;
}

.signup fieldset {
  padding: 9px;
  background: transparent;
}

.signup h3 {
  text-align: center;
  margin-bottom: 30px;
  font-size: 45px;
}

.signup .label {
  margin-bottom: 5px;
  display: flex;
  justify-content: flex-start;
  font-size: 5px;
}

.signup i {
  font-size: 24px;
  padding: 10px;
  background: black;
  color: white;
}

.signup input {
  font-size: 20px;
  margin-left: 10px;
  line-height: 10px;
  border: none;
  border-bottom: 1px solid black;
  padding: 5px;
  outline: 0;
}

.fatherbtn .button {
  width: 250px;
  height: 50px;
  background: black;
  padding: 10px;
  color: white;
  border: 1.5px solid black;
  display: flex;
  justify-content: center;
  transition: 0.3 all ease;
}

.button:hover {
  font-weight: bold;
  cursor: pointer;
}